<template>
  <div class="subPage">
    <div class="pageTitle">医疗废物交接统计</div>
    <div>
      <el-date-picker v-model="dates" type="month" laceholder="选择月" value-format="MM"></el-date-picker>
      <el-button class="btn_search" type="success" round @click="search">搜索</el-button>
    </div>
    <div class="contents">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="cjperson" label="人员名称" width="220"></el-table-column>
        <el-table-column prop="typeName" label="废物类型" width="220"></el-table-column>
        <el-table-column prop="weight" label="重量" width="220"></el-table-column>
        <el-table-column prop="count" label="数量"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      dates: '',
      tableData:[]
    };
  },
  methods: {
    search(){
      var thisObject = this;
      var data = {
        type:'ycxxtj',
        startdate:thisObject.dates
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          for(var i = 0; i < res.data.length; i++){
            switch (res.data[i].type){
              case 1:
                res.data[i].typeName = "感染性";
                break;
              case 2:
                res.data[i].typeName = "损伤性";
                break;
              case 3:
                res.data[i].typeName = "病理性";
                break;
              case 4:
                res.data[i].typeName = "化学性";
                break;
              case 5:
                res.data[i].typeName = "药物性";
                break;
              default:
            }
          }
          thisObject.tableData = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {

  }

}
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/subPage';
</style>
